import React from 'react';
import { Link } from 'react-router-dom';
import '../css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap.bundle.min.js';
import 'bootstrap-icons/font/bootstrap-icons.css';
import '@fortawesome/fontawesome-free/css/all.min.css';
import '../css/home.css';
import '../css/style.css';

const Navigation = ({ onLogout }) => {
  return (
      <div className="container-xxl position-relative p-0">
        <nav className="navbar navbar-expand-lg navbar-light px-4 px-lg-5 py-1 py-lg-0">
          <Link to="/" className="navbar-brand p-0">
            <h1 className="m-0"><i className="fa fa-search me-2"></i>KT<span className="fs-5">Aivle</span></h1>
          </Link>
          <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarCollapse">
            <span className="fa fa-bars"></span>
          </button>
          <div className="collapse navbar-collapse" id="navbarCollapse">
            <div className="navbar-nav ms-auto py-0">
              <Link to="/" className="nav-item nav-link"></Link>
              <Link to="/lecture" className="nav-item nav-link"></Link>
              <Link to="/report" className="nav-item nav-link"></Link>
              <Link to="/profile" className="nav-item nav-link"></Link>
             {/*<Link to="/upload" className="nav-item nav-link"></Link>*/}  {/*    */}
            </div>
            <button onClick={onLogout} className="btn btn-secondary text-light rounded-pill py-2 px-4 ms-3"></button>
          </div>
        </nav>
      </div>
  );
};

export default Navigation;
