import React, { useEffect, useState } from 'react';
import axios from 'axios';

const Video = () => {
    const [videoUrl, setVideoUrl] = useState('');

    useEffect(() => {
        const fileName = 'machine.mp4';

        axios.get(`/api/video-url?fileName=${fileName}`)
            .then(response => {
                const fetchedUrl = response.data;
                console.log("Fetched Video URL:", fetchedUrl);
                setVideoUrl(fetchedUrl);
            })
            .catch(error => {
                console.error("Error fetching video URL:", error);
            });
    }, []);

    return (
        <div style={{ display: 'flex', height: '100vh' }}>
            <div className="lecture-video-container" style={{ flex: '75%', position: 'relative', boxShadow: '0 0 20px rgba(0, 0, 0, 0.2)', borderRadius: '10px', overflow: 'hidden', backgroundColor: '#fff' }}>
                <video className="lecture-video" controls style={{ width: '100%', height: '100%', objectFit: 'cover' }} src={videoUrl}>
                    Your browser does not support the video tag.
                </video>
            </div>
            <div className="chat-container" style={{ flex: '25%', backgroundColor: '#fff', borderLeft: '2px solid #ddd', display: 'flex', flexDirection: 'column' }}>
                <div className="chat-header" style={{ backgroundColor: '#007bff', color: '#fff', padding: '10px', textAlign: 'center', fontWeight: 'bold' }}>Chat</div>
                <div className="chat-messages" id="chat-messages" style={{ flex: 1, padding: '10px', overflowY: 'auto', borderBottom: '1px solid #ddd' }}></div>
                <div className="chat-input" style={{ display: 'flex', padding: '10px', borderTop: '1px solid #ddd' }}>
                    <input type="text" id="chat-input" placeholder="Type a message..." style={{ flex: 1, padding: '10px', border: '1px solid #ddd', borderRadius: '5px', marginRight: '10px' }} />
                    <button onClick={sendMessage} style={{ padding: '10px 15px', backgroundColor: '#007bff', color: '#fff', border: 'none', borderRadius: '5px', cursor: 'pointer' }}>Send</button>
                </div>
            </div>
        </div>
    );
};

function sendMessage() {
    const input = document.getElementById('chat-input');
    const messages = document.getElementById('chat-messages');
    if (input.value.trim() !== '') {
        const message = document.createElement('div');
        message.textContent = input.value;
        messages.appendChild(message);
        input.value = '';
        messages.scrollTop = messages.scrollHeight;
    }
}

export default Video;
