import React from 'react';
import '../css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap.bundle.min.js';
import 'bootstrap-icons/font/bootstrap-icons.css';
import '@fortawesome/fontawesome-free/css/all.min.css';
import '../css/home.css';
import '../css/style.css';

const Testimonials = () => {
    return (
        <div className="container-xxl bg-primary testimonial py-5 my-5 wow fadeInUp" data-wow-delay="0.1s">
            <div className="container py-5 px-lg-5">
                <div id="testimonialCarousel" className="carousel slide" data-bs-ride="carousel">
                    <div className="carousel-inner">
                        <TestimonialItem
                            content="    "
                            name=""
                            position="KT"
                            imgSrc="img/testimonial-1.jpg"
                            active={true}
                        />
                        <TestimonialItem
                            content="Dolor et eos labore, stet justo sed est sed. Diam sed sed dolor stet amet eirmod eos labore diam"
                            name="student"
                            position="Profession"
                            imgSrc="img/testimonial-2.jpg"
                        />
                        <TestimonialItem
                            content="Dolor et eos labore, stet justo sed est sed. Diam sed sed dolor stet amet eirmod eos labore diam"
                            name="Client"
                            position="Profession"
                            imgSrc="img/testimonial-3.jpg"
                        />
                        <TestimonialItem
                            content="Dolor et eos labore, stet justo sed est sed. Diam sed sed dolor stet amet eirmod eos labore diam"
                            name="aivle"
                            position="Profession"
                            imgSrc="img/testimonial-4.jpg"
                        />
                    </div>
                    <button className="carousel-control-prev" type="button" data-bs-target="#testimonialCarousel" data-bs-slide="prev">
                        <span className="carousel-control-prev-icon" aria-hidden="true"></span>
                        <span className="visually-hidden">Previous</span>
                    </button>
                    <button className="carousel-control-next" type="button" data-bs-target="#testimonialCarousel" data-bs-slide="next">
                        <span className="carousel-control-next-icon" aria-hidden="true"></span>
                        <span className="visually-hidden">Next</span>
                    </button>
                </div>
            </div>
        </div>
    );
};

const TestimonialItem = ({ content, name, position, imgSrc, active }) => {
    return (
        <div className={`carousel-item ${active ? 'active' : ''}`}>
            <div className="testimonial-item bg-transparent border rounded text-white p-4">
                <i className="fa fa-quote-left fa-2x mb-3"></i>
                <p>{content}</p>
                <div className="d-flex align-items-center">
                    <img className="img-fluid flex-shrink-0 rounded-circle" src={imgSrc} style={{ width: "50px", height: "50px" }} alt={name} />
                    <div className="ps-3">
                        <h6 className="text-white mb-1">{name}</h6>
                        <small>{position}</small>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default Testimonials;