import React from 'react';
import { Link } from 'react-router-dom';
import '../css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap.bundle.min.js';
import 'bootstrap-icons/font/bootstrap-icons.css';
import '@fortawesome/fontawesome-free/css/all.min.css';
import '../css/home.css';
import '../css/style.css';

const Hero = () => (
    <div className="container-xxl py-5 bg-primary hero-header mb-5">
        <div className="container my-5 py-5 px-lg-5">
            <div className="row g-5 py-5">
                <div className="col-lg-6 text-center text-lg-start">
                    <h3 className="text-white mb-4 animated zoomIn" style={{whiteSpace: "nowrap"}}>LMS   </h3>
                    <p className="text-white pb-3 animated zoomIn">    !</p>
                    <Link to="" className="btn btn-light py-sm-3 px-sm-5 rounded-pill me-3 animated slideInLeft">Free Quote</Link>
                    <Link to="" className="btn btn-outline-light py-sm-3 px-sm-5 rounded-pill animated slideInRight">Contact Us</Link>
                </div>
                <div className="col-lg-6 text-center text-lg-start">
                    <img className="img-fluid" src="img/hero.png" alt="" />
                </div>
            </div>
        </div>
    </div>
);

export default Hero;