import React, { useState } from 'react';
import { registerUser } from '../api';
import './Login.css';

const RegisterForm = ({ toggleForm }) => {
    const [name, setName] = useState('');
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [registrationMessage, setRegistrationMessage] = useState('');

    const handleRegister = async (e) => {
        e.preventDefault();
        try {
            const userData = { name, email, password };
            const result = await registerUser(userData);
            setRegistrationMessage(result);
            setName('');
            setEmail('');
            setPassword('');
        } catch (error) {
            console.error('Error registering user:', error);
            setRegistrationMessage('Registration failed');
        }
    };

    return (
        <form className="login__create block" onSubmit={handleRegister}>
            <h1 className="login__title">Create Account</h1>
            <div className="login__box">
                <i className='bx bx-user login__icon'></i>
                <input
                    type="text"
                    placeholder="Username"
                    className="login__input"
                    value={name}
                    onChange={(e) => setName(e.target.value)}
                    required
                />
            </div>
            <div className="login__box">
                <i className='bx bx-at login__icon'></i>
                <input
                    type="email"
                    placeholder="Email"
                    className="login__input"
                    value={email}
                    onChange={(e) => setEmail(e.target.value)}
                    required
                />
            </div>
            <div className="login__box">
                <i className='bx bx-lock login__icon'></i>
                <input
                    type="password"
                    placeholder="Password"
                    className="login__input"
                    value={password}
                    onChange={(e) => setPassword(e.target.value)}
                    required
                />
            </div>
            <button type="submit" className="login__button">Sign Up</button>
            <div>
                <span className="login__account">Already have an Account?</span>
                <span className="login__signup" onClick={toggleForm}>Sign In</span>
            </div>
            <p>{registrationMessage}</p>
            <div className="login__social">
                <a href="#" className="login__social--icon"><i className='bx bxl-facebook'></i></a>
                <a href="#" className="login__social--icon"><i className='bx bxl-twitter'></i></a>
                <a href="#" className="login__social--icon"><i className='bx bxl-google'></i></a>
                <a href="#" className="login__social--icon"><i className='bx bxl-github'></i></a>
            </div>
        </form>
    );
};

export default RegisterForm;
