import React from 'react';

const NoticeSection = () => (
    <div className="col-md-6">
        <div className="section-box">
            <h6 className="section-title"></h6>
            <a href="#" className="see-all">See all</a>
            <ul className="notice-list">
                <NoticeItem badge="" text="LMS    " time="10:25 am" />
                <NoticeItem badge="" text="  " time="12:35 pm" />
                <NoticeItem badge="" text="    " time="04:30 pm" />
            </ul>
        </div>
    </div>
);

const NoticeItem = ({ badge, text, time }) => (
    <li>
        <span className="badge">{badge}</span>
        <span className="notice-text">{text}</span>
        <span className="notice-time">{time}</span>
    </li>
);

export default NoticeSection;