import React, { useEffect } from 'react';
import Chart from 'chart.js/auto';


const ProgressSection = () => {
    useEffect(() => {
        const progressValues = [32, 43, 67, 56];
        const chartColors = ['#007bff', '#e9ecef'];

        for (let i = 1; i <= 4; i++) {
            const chartId = `chart${i}`;
            const ctx = document.getElementById(chartId);

            if (ctx) {
                // Destroy existing chart if it exists
                const existingChart = Chart.getChart(ctx);
                if (existingChart) {
                    existingChart.destroy();
                }

                // Create new chart
                new Chart(ctx, {
                    type: 'doughnut',
                    data: {
                        datasets: [{
                            data: [progressValues[i-1], 100 - progressValues[i-1]],
                            backgroundColor: chartColors,
                            borderWidth: 0
                        }]
                    },
                    options: {
                        cutout: '80%',
                        responsive: true,
                        maintainAspectRatio: false,
                        plugins: {
                            legend: {
                                display: false
                            },
                            tooltip: {
                                enabled: false
                            }
                        }
                    }
                });
            }
        }

        // Clean up: destroy all charts when component unmounts
        return () => {
            for (let i = 1; i <= 4; i++) {
                const ctx = document.getElementById(`chart${i}`);
                const existingChart = Chart.getChart(ctx);
                if (existingChart) {
                    existingChart.destroy();
                }
            }
        };
    }, []);

    return (
        <div className="col-md-6">
            <div className="section-box">
                <h6 className="section-title"> </h6>
                <div className="progress-list">
                    <ProgressItem title="RAG  " percentage="32" chartId="chart1" />
                    <ProgressItem title="Vector DB " percentage="43" chartId="chart2" />
                    <ProgressItem title=" " percentage="67" chartId="chart3" />
                    <ProgressItem title=" " percentage="56" chartId="chart4" />
                </div>
            </div>
        </div>
    );
};

const ProgressItem = ({ title, percentage, chartId }) => (
    <div className="progress-item">
        <div className="progress-chart">
            <canvas id={chartId}></canvas>
        </div>
        <div className="progress-info">
            <span className="course-title">{title}</span>
            <span className="progress-percentage">{percentage}%</span>
        </div>
    </div>
);

export default ProgressSection;
