import React, { useState } from 'react';
import axios from 'axios';
import './Login.css';

const AdminLoginForm = ({ onLogin }) => {
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [loginMessage, setLoginMessage] = useState('');

    const handleLogin = async (e) => {
        e.preventDefault();
        try {
            const adminData = { email, password };
            const response = await axios.post('http://localhost:8080/admin/login', adminData);
            if (response.data === 'Admin login successful') {
                setLoginMessage('Login successful');
                onLogin(email);
                window.location.href = '/upload';
            } else {
                setLoginMessage('Login failed');
            }
        } catch (error) {
            console.error('Error logging in admin:', error);
            setLoginMessage('Login failed');
        }
    };

    return (
        <form className="login__register block" onSubmit={handleLogin}>
            <h1 className="login__title">Admin Sign In</h1>
            <div className="login__box">
                <i className='bx bx-user login__icon'></i>
                <input
                    type="email"
                    placeholder="Email"
                    className="login__input"
                    value={email}
                    onChange={(e) => setEmail(e.target.value)}
                    required
                />
            </div>
            <div className="login__box">
                <i className='bx bx-lock login__icon'></i>
                <input
                    type="password"
                    placeholder="Password"
                    className="login__input"
                    value={password}
                    onChange={(e) => setPassword(e.target.value)}
                    required
                />
            </div>
            <button type="submit" className="login__button">Sign In</button>
            <p>{loginMessage}</p>
        </form>
    );
};

export default AdminLoginForm;
